<template>
    <div class="simu-wrapper">
        <div class="simu-header">
            <div class="simu-header-box">
                <div class="accessory">
                    附件
                </div>
                <div class="count-down">{{countDown}}</div>
                <div class="current-state">
                    <div class="start">
                        <p class="start-desc">开始时间</p>
                        <p class="time">{{startTime}}</p>
                    </div>
                    <div class="state">
                        <p class="state-text">状态</p>
                        <p class="state-desc">未考</p>
                    </div>
                    <div class="end">
                        <p class="end-desc">结束时间</p>
                        <p class="time">{{endTime}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="topic-box">
            <div class="topic">
                <div class="topic-item" v-for="(items,index) in topic" :key="index">
                    <div class="item-name">{{items.itemName}}</div>
                    <div class="topic-content-box">
                        <div class="topic-content" v-for="(item,i) in items.itemTopic" :key="i">
                            <p class="item-title">{{item.title}}</p>
                            <div class="item-content">
                                <p v-for="(con,ci) in item.content" :key="ci">{{con}}</p>
                            </div>
                            <p class="item-text">{{item.text}}</p>
                            <p class="item-options">{{item.options}}</p>
                            <p class="item-condition">{{item.condition}}</p>
                        </div>
                        <div class="item-line">
                            <img src='../../assets/img/line.png' alt="">
                        </div>
                    </div>
                </div>
                <div class="submit" @click="submitAnswer">提交答卷</div>
            </div>
        </div>
        <!-- 对话框 -->
        <el-dialog
            title="温馨提示"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            <span>{{hint}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button v-if="cancelShow" @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click='leaveTrue'>确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<style scoped>
    @import './Simu.css';
</style>

<script>
import { setInterval, clearInterval } from 'timers';
export default {
    // props: {
    //     startTime: {
    //         type: String,
    //         required: true
    //     }
    // },
    data(){
        return {
            countDown: '120:00',
            dialogVisible: false,
            timer: '',
            cancelShow: false,
            allow: false,
            hint: '考试未结束，无法查看其他页面!',
            topic: [
                {
                    itemName: '一、单选题管理（共20分）',
                    itemTopic: [
                        {
                            title: '《这个星期天》',
                            content: [
                                '星期天，妈妈带我上街。我特意拿出生日时姑姑送给我的扣花戴在胸前。这扣花可真漂亮，一颗颗晶莹的水晶石镶嵌在金孔雀形的扣花里。动一动，“金孔雀”就闪闪发光，非常惹人喜爱。街上(     )，商场的人更多。我和妈妈好不容易才挤进柜台。正当我被(    )的衣料吸引时，肩膀被推了一下。我气得猛一回头，冲着推我的那人嚷道：“挤什么?”没想到那人不但不恼，反而微笑着对我点点头，用手指着我的前胸，嘴里一个劲地“啊，啊……”并招手要我出去。我低头一看“糟糕!我的扣花没了!”',
                                '我不耐烦地挤出人群。只见那人大约三十来岁，个子不高，眯着一双不太有神的眼睛。他一见我便迫不及待地将手中的一件东西送到眼前。我接过扣花定睛一看，咦？这难道不是我丢的那枚扣花吗？我气乎乎地问：“你这人怎么随便拿我的扣花？”那人见我发火，嘴里一个劲儿地“啊，啊……”脸上现出焦急的神情。他一会儿指指我手中的扣花，一会儿又弯腰走几步，做出捡东西的样子，好像要告诉我什么。看他的表情和动作，我明白了他是个聋哑人，是他拾到了我被挤掉的扣花，并交还（hai huan）给我，可我竟……“芳芳，你怎么站着发呆”妈妈的声音打断了我的思绪，我忙（       ），人呢?我还没有感谢他呢，他却不见了。我随妈妈走出商场，一边走一边找那个人，希望看到那双不太有神的眼睛。多么可敬的聋哑人啊!那(    )的高贵品质深深打动了我的心。然而我没有找见他，只有我手中被汗水浸湿的扣花在阳光照射下熠熠闪光，显得更加美丽。'
                            ],
                            text: '1、把下面的词语准确地填写在文中的(    )里。',
                            options: 'A. 人山人海  B. 拾金不昧； C.五颜六色； D.四下环顾；',
                            condition: '1.作业条件一-1——————————。'
                        },
                        {
                            title: '《快乐与感触》',
                            content: [
                                '依稀记得在我两三岁的时候，我天天跟着妈妈到学校里玩耍，那时妈妈在学校里给幼儿班代课。课间一群比我大点的孩子们总是围着我说啊、笑啊、眯眯眼做做鬼脸什么的，也有不停地给我手中或嘴里塞干粮的。他们一听到铃声嘴里哦———” 着飞也似的进了教室。于是我便一人悠闲自在地在校园里溜达：一步一步地踱到东边看看美丽的花儿；爬到西边的球台上翻着晒晒太阳；听到南边教室里悠扬的歌声，于是又跑到窗户下踮着脚使劲儿地仰起头向里看；仰倦了头嘴里嘟嘟地哼着，若无其事的来到北面那两块瓷砖镶嵌的大地图下，看着那些花花绿绿的条条块块，也不知道是些什么。如今，我已是那时年龄的四倍了，仍在这熟悉温暖的校园里，那时一切不懂的，今天都明白了。那时的快乐依在，那时的天真依在。不过现在我所看到的、听到的、感悟的比那时多得多了。清晨的校园，阳光钻透东边茂密的柳林，斑驳的光点印在绿绿的草坪上。无数只鸟儿横着或倒挂在柔柔的柳条上凑响清脆的晨曲。在通向教师办公大楼的水泥道上，陆陆续续晃过一群高大的身影———我们的老师，他们又上班去了。当校园正中升起鲜艳的五星红旗时，悦耳的歌声和朗朗的读书声早已把校园装点得生机勃勃。我再不需要像过去那样踮脚仰头地去向往了。我尽心地在这宽敞明亮的教室里学习，聆听着老师的教诲，享受着群体的温暖与关爱。课间，我们三三俩俩去拉着或牵着幼儿班的那些小娃娃，说啊、笑啊、眯眯眼做做鬼脸什么的，也有不停地给他们手中或嘴里塞泡泡糖的。有一天，我把三四个小娃娃牵到北面那两块瓷砖镶嵌的大地图下，学着老师的样子摇头晃脑、指着地图比比划划地讲：这是中国，这是长江、那是黄河……，我们的学校在这里，要记住，别忘记。看我的手好大，把一个省都罩住了。” 小娃娃们叽叽喳喳地笑个不停，我也笑得前俯后仰。上课铃响了，我们“哦———”着飞也似的进了教室。那天我们进了教室，唱完了一首长长的歌，没见老师来，于是我站起来对大家说：“大家先读读书吧，我去办公室看看”。话音刚落，只见李老师一瘸一拐地走进教室。同学们注视着他的脚。原来他脚上缠着一圈大大的药纱布。我们明白他的脚受伤了。他开始给我们讲课，和往常一样站着，不时还转去转来。一会儿，我从他变化了的语调中感到：他的脚疼痛难忍了。我忍不住环视一下四周，发现所有同学的眼里都水晶晶的，此刻我心头一热，眼泪夺眶而出。这天我在日记中写道：“在我们快乐的时候，我们的老师也许正痛苦着；在我们获取的时候，我们的老师正在奉献着；在我们成长的时候，我们的老师正在消亡着。”太阳依旧从东边升起，灿烂的阳光总是洒满校园。当我的年龄到了是现在的若干倍的时候，我会依然记起今天的快乐与感触。'
                            ],
                            text: '2、通过短文来找属于ABB的词？',
                            options: 'A.花花绿绿           B比比划划           C叽叽喳喳           D水晶晶',
                            condition: '2.作业条件一_2—————。'
                        }
                    ]
                }
            ]
        }
    },
    methods: {
        getStartTime(){
            const date = new Date();
            const hours = date.getHours();
            const minutes = date.getMinutes();
            const seconds = date.getSeconds();
            this.startTime = hours + ':' + minutes + ':' + seconds
        },
        // 提交答卷
        submitAnswer(){
            const minute = localStorage.getItem('minute');
            const seconds = localStorage.getItem('seconds');
            this.hint = `距离考试结束还有${minute}分钟${seconds}秒，确认提交答卷吗？`;
            this.cancelShow = true;
            this.allow = true
            this.dialogVisible = true
        },
        // 设置倒计时
        _changeCountDown(minute,seconds){
            minute = parseInt(minute);
            seconds = parseInt(seconds)
            this.timer = setInterval(() =>{
                if(seconds == 0){
                    seconds = 60;
                    minute -= 1;
                }
                seconds = seconds - 1;
                let stringSeconds = seconds;
                let stringMinute = minute
                if(stringSeconds < 10){
                    stringSeconds = '0' + stringSeconds
                }
                if(stringSeconds == 60){
                    stringSeconds = '00'
                }
                if(stringMinute < 10){
                    stringMinute = '0' + stringMinute
                }
                this.countDown = stringMinute + ':' + stringSeconds;
                // 每次都要进行存储
                localStorage.setItem('minute',minute);
                localStorage.setItem('seconds',seconds)
                if(minute == 0 && seconds == 0){
                    clearInterval(this.timer);
                    localStorage.removeItem('minute');
                    localStorage.removeItem('seconds');
                    this.hint = '时间已到,点击确认提交答卷';
                    this.allow = true;
                    this.dialogVisible = true;
                }
            },1000)
        },
        // 倒计时已到 考试结束
        examEnd(){
            this.dialogVisible2 = false
        },
        // 用户离开
        leaveTrue(){
           if(this.allow){
                clearInterval(this.timer)
                localStorage.removeItem('minute');
                localStorage.removeItem('seconds');
                this.dialogVisible = false;
                let url = location.href;
                const index = url.indexOf('index')
                url = url.slice(0,index);
                console.log(url)
                // this.$router.push({name: 'Examend'})
                // this.$router.back()
                location.href = url + 'examend?a=1'
           }else{
               this.dialogVisible = false;
           }
        },
        handleClose(done) {
            // this.$confirm('确认关闭？')
            // .then(_ => {
                done();
            // })
            // .catch(_ => {});
        }
    },
    computed: {
        startTime(){
            return this.$route.query.Time.startTime
        },
        endTime(){
            return this.$route.query.Time.endTime
        }
    },
    created(){
        console.log(location.href)
    },
    beforeMount(){
        //判断取值 防止用户刷新
        let minute;
        let seconds ;
        if(localStorage.getItem('seconds')){
            minute = localStorage.getItem('minute');
            seconds = localStorage.getItem('seconds')
        }else{
            minute = parseInt(this.countDown);
            seconds = 0
            localStorage.setItem('minute',minute)
            localStorage.setItem('seconds',seconds)
        }
        this._changeCountDown(minute,seconds);
    },
    // 离开路由守卫
    beforeRouteLeave(to, from, next) {
        // 导航离开该组件的对应路由时调用
        // 可以访问组件实例 `this`
        // alert(to.path)
        this.allow = false;
        this.dialogVisible = true;
        next(false)
    }
}
</script>