<style scoped>
    @import './Index.css';
</style>
<script>
import header from '../com/header'
export default {
    components: {
        "z-header": header
    },
    data(){
        return {
            path: require('../../assets/img/LOGO.png'),
            bgColor: '#fff',
            color: '#777',
            userName: '用户名123'
            // menuRight: ['<i class="el-icon-s-home"></i>首页','个人中心'],
            // currentIndex: 0,
            // menuRight2: ['首页','个人中心']
        }
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        currentMenu(index,indexPath){
            console.log(index,indexPath)
            const date = this._getStartTime()
            this.bgColor = '#0070d8',
            this.color = '#fff',
            this.path = require('../../assets/img/LOGO-white.png');
            if(index == '1-1'){
                this.$router.push({path: '/index/simu',query: {Time: date}})
            }
            if(index == '1-2'){
                this.$router.push({path: '/index/simu',query: {Time: date}})
            }
        },
        rightMenu(index){
            this.currentIndex = index;
            if(index == 0){
                this.$router.push('/index/index-in')
            }
        },
        _getStartTime(){
            const date = new Date();
            const endDate = new Date(date.getTime() + 7200*1000);
            let hours = date.getHours();
            let minutes = date.getMinutes();
            let seconds = date.getSeconds();
            let endHours = endDate.getHours();
            let endMinutes = endDate.getMinutes();
            let endSeconds = endDate.getSeconds();
            if(hours < 10)  hours = '0' + hours
            if(minutes < 10) minutes = '0' + minutes
            if(seconds < 10) seconds = '0' + seconds
            if(endHours < 10) endHours = '0' + endHours
            if(endMinutes < 10) endMinutes = '0' + endMinutes
            if(endSeconds < 10) endSeconds = '0' + endSeconds
            const startTime = hours + ':' + minutes + ':' + seconds;
            const endTime = endHours + ':' + endMinutes + ':' + endSeconds;
            return {startTime,endTime};
        }
    }
}
</script>
