<template>
    <div class="answer-wrapper">
        <z-header :srcPath='path' :isShow='false' :userName='userName'
                  :currentBgColor='bgColor' :currentFontColor='color'></z-header>
        <div class="answer-header">
            <div class="answer-header-box">
                <div class="accessory">
                    答题卡
                </div>
                <div class="count-down">{{countDown}}</div>
                <div class="current-state">
                    <div class="start">
                        <p class="start-desc">开始时间</p>
                        <p class="time">09：13</p>
                    </div>
                    <div class="state">
                        <p class="state-text">状态</p>
                        <p class="state-desc">未考</p>
                    </div>
                    <div class="end">
                        <p class="end-desc">结束时间</p>
                        <p class="time">11：13</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="topic-box">
            <div class="topic">
                <div class="topic-item" v-for="(items,index) in topicMess" :key="index">
                    <div class="item-name">{{items.itemName}}</div>
                    <div class="topic-content-wrapper">
                        <ul class="topic-content-ul">
                            <li v-for="(item,i) in items.seedItem" :key="i">
                                <p class="item-mess">{{item.content}}<span class="item-desc">{{item.itemDesc}}</span></p>
                            </li>
                        </ul>
                        <div class="item-line">
                            <img src='../../assets/img/line2.png' alt="">
                        </div>
                    </div>
                </div>
                <div class="submit" @click="submitAnswer">提交答卷</div>
            </div>
        </div>
        <div class="warm" v-if="isShow">
            <el-row>
                <el-col :span="24" >
                    <el-card :body-style="{ padding: '20px 0px' }">
                    <img src="../../assets/img/clock.png" class="image">
                    <div style="padding: 14px;">
                        <div class="warm-desc">
                            <p class="time">考试剩余时间<span>10</span>分钟</p>
                            <p class="please">请尽快提交试卷</p>
                            <p class="desc">倒计时为0后系统将自动提交，结束考试</p>
                        </div>
                    </div>
                    </el-card>
                </el-col>
            </el-row>
        </div>
        <!-- 对话框 -->
        <el-dialog
            title="温馨提示"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            <span>{{hint}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button v-if="cancelShow" @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click='leaveTrue'>确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<style scoped>
    .answer-wrapper{
        background: #f7f7f7;
        /* position: relative; */
    }
    .answer-wrapper .warm{
        position: fixed;
        top: 14vh;
        right: 10vw;
        text-align: center
    }
    .answer-wrapper .warm .warm-desc .time{
        padding-bottom: 10px;
        border-bottom: 2px solid #ccc;
        font-size: 16px;
    }
    .answer-wrapper .warm .warm-desc .time span{
        color: #e11f00;
        font-weight: 600
    }
    .answer-wrapper .warm .warm-desc .please,
    .answer-wrapper .warm .warm-desc .desc{
        color: #888;
        font-size: 12px;
        padding: 5px 0;
    }
    .answer-wrapper .answer-header{
        width: 100%;
        height: 7vh;
        border-bottom: 1px solid #ddd;
        background: #fff;
        margin: auto
    }
    .answer-wrapper .answer-header-box{
        width: 60%;
        height: 100%;
        margin: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .answer-header .accessory{
        color: #0070d8;
        height: 7vh;
        line-height: 7vh;
        font-weight: 600;
        border-bottom: 3px solid #0070d8
    }
    .answer-header .count-down{
        font-size: 20px;
        font-weight: 600;
        color: #d22d2d;
        background: #f6d4d4;
        height: 5vh;
        padding: 0 20px;
        line-height: 5vh
    }
    .answer-header .current-state{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        width: 18%;
        text-align: center
    }
    .current-state .start .start-desc,
    .current-state .end .end-desc{
        padding: 2px 12px;
        color: #fff;
        background: #0070d8;
        font-size: 12px;
    }
    .current-state .start .time,
    .current-state .end .time{
        font-size: 14px;
        color: #0070d8;
        margin-top: 4px;
    }
    .current-state .state .state-text{
        padding: 2px 12px;
        color: #fff;
        background: #cc1313;
        font-size: 12px;
    }
    .current-state .state .state-desc{
        font-size: 14px;
        color: #333;
        margin-top: 4px;
    }
    .answer-wrapper .topic{
        width: 60%;
        margin: auto;
        padding-bottom: 5vh;
        margin-top: 4vh
    }    
    .answer-wrapper .topic-item{
        margin-top: 3vh;
        background: #ffff;
    }
    .answer-wrapper .topic .topic-content-wrapper{
        padding: 3vh 0 4vh 5vh
    }
    .answer-wrapper .topic-item .item-name{
        height: 4vh;
        background: #0070d8;
        color: #fff;
        text-indent: 0.5em;
        line-height: 4vh;
        font-size: 14px;
    }
    .answer-wrapper .topic .topic-item{
        position: relative;
        box-sizing: border-box;
    }
    .answer-wrapper .topic .topic-item .item-line{
        position: absolute;
        height: 100%;
        right: -2px;
        top: 5vh
    }
    .answer-wrapper .topic .topic-item .item-line img{
        width: 10px;
        height: 88%;
    }
    .answer-wrapper .topic .topic-content-ul li{
        padding: 10px 0;
        font-size: 15px;
        color: #333
    }
    .answer-wrapper .topic .topic-content-ul li .item-desc{
        color: #0070d8
    }
    .answer-wrapper .topic .submit{
        width: 6vw;
        height: 4vh;
        background: #0070d8;
        border-radius: 100px;
        color: #fff;
        text-align: center;
        line-height: 4vh;
        margin: 3vh auto 0;
        cursor: pointer;
        user-select: none;
    }
</style>
<script>
import header from '../com/header'
import { setTimeout } from 'timers';
export default {
    components: {
        "z-header": header
    },
    data(){
        return{
            path: require('../../assets/img/userPhoto.png'),
            bgColor: '#0070d8',
            color: '#fff',
            userName: '用户123',
            countDown: '60:00',
            isShow: false,
            dialogVisible: false,
            cancelShow: true,
            timer: '',
            hint: '是否确认提交？',
            allow: false,
            topicMess: [
                {
                    itemName: '一、单选题管理（共20分）',
                    seedItem: [
                        {content: '1.作业条件一-1（        ）。', itemDesc: '(附件1第1题)'},
                        {content: '2.作业条件一-2（        ）。', itemDesc: '(附件2第2题)'},
                        {content: '3.作业条件一-3（        ）。', itemDesc: '(附件3第3题)'},
                        {content: '4.作业条件一-4（        ）。', itemDesc: '(附件4第4题)'},
                        {content: '5.作业条件一-5（        ）。', itemDesc: '(附件5第5题)'},
                        {content: '6.作业条件一-6（        ）。', itemDesc: '(附件6第6题)'}
                    ]
                },
                {
                    itemName: '二、多选题管理（共20分）',
                    seedItem: [
                        {content: '7.作业条件三-1（           ）（           ）（           ）（           ）。', itemDesc: '(附件7第7题)'},
                        {content: '8.作业条件三-1（           ）（           ）（           ）（           ）。', itemDesc: '(附件8第8题)'},
                        {content: '9.作业条件三-1（           ）（           ）（           ）（           ）。', itemDesc: '(附件9第9题)'},
                        {content: '10.作业条件三-1（           ）（           ）（           ）（           ）。', itemDesc: '(附件10第10题)'},
                        {content: '11.作业条件三-1（           ）（           ）（           ）（           ）。', itemDesc: '(附件11第11题)'},
                        {content: '12.作业条件三-1（           ）（           ）（           ）（           ）。', itemDesc: '(附件12第12题)'}
                    ]
                },
                {
                    itemName: '三、判断题管理（共20分）',
                    seedItem: [
                        {content: '13.作业条件五-13（        ）。', itemDesc: '(附件13第13题)'},
                        {content: '14.作业条件五-14（        ）。', itemDesc: '(附件14第14题)'},
                        {content: '15.作业条件五-15（        ）。', itemDesc: '(附件15第15题)'},
                        {content: '16.作业条件五-16（        ）。', itemDesc: '(附件16第16题)'},
                        {content: '17.作业条件五-17（        ）。', itemDesc: '(附件17第17题)'},
                        {content: '18.作业条件五-18（        ）。', itemDesc: '(附件18第18题)'}
                    ]
                }
            ]
        }
    },
    methods: {
        submitAnswer(){
            const minute = localStorage.getItem('cardMinute');
            const seconds = localStorage.getItem('cardSeconds');
            this.hint = `距离考试结束还有${minute}分钟${seconds}秒，确认提交答卷吗？`;
            this.cancelShow = true;
            this.allow = true;
            this.dialogVisible = true
        },
         _changeCountDown(minute,seconds){
            minute = parseInt(minute);
            seconds = parseInt(seconds)
            this.timer = setInterval(() =>{
                if(minute == 10 && seconds == 0){
                    this.isShow = true;
                    setTimeout(() =>{
                        this.isShow = false
                    },20000)
                }
                if(seconds == 0){
                    seconds = 60;
                    minute -= 1;
                }
                seconds = seconds - 1;
                let stringSeconds = seconds;
                let stringMinute = minute
                if(stringSeconds < 10){
                    stringSeconds = '0' + stringSeconds
                }
                if(stringSeconds == 60){
                    stringSeconds = '00'
                }
                if(stringMinute < 10){
                    stringMinute = '0' + stringMinute
                }
                this.countDown = stringMinute + ':' + stringSeconds;
                // 每次都要进行存储
                localStorage.setItem('cardMinute',minute);
                localStorage.setItem('cardSeconds',seconds);
                
                if(minute == 0 && seconds == 0){
                    clearInterval(this.timer);
                    localStorage.removeItem('cardMinute');
                    localStorage.removeItem('cardSeconds');
                    this.hint = '时间已到,点击确认提交答卷';
                    this.allow = true;
                    this.cancelShow = false
                    this.dialogVisible = true;
                }
            },1000)
        },
        leaveTrue(){
           if(this.allow){
                clearInterval(this.timer)
                localStorage.removeItem('minute');
                localStorage.removeItem('seconds');
                this.dialogVisible = false;
                this.$router.push('/examend')
                // this.$router.back()
                // location.href = 'http://localhost:8080/index'
           }else{
               this.dialogVisible = false;
           }
        },
        handleClose(done){
            done()
        }
    },
    mounted(){
        let cardMinute;
        let cardSeconds ;
        if(localStorage.getItem('cardSeconds')){
            cardMinute = localStorage.getItem('cardMinute');
            cardSeconds = localStorage.getItem('cardSeconds')
        }else{
            cardMinute = parseInt(this.countDown);
            cardSeconds = 0
            localStorage.setItem('cardMinute',cardMinute)
            localStorage.setItem('cardSeconds',cardSeconds)
        }
        this._changeCountDown(cardMinute,cardSeconds);
    }
}
</script>