import Vue from 'vue'
import Router from 'vue-router'
import Login from './components/Login'
import Index from './components/index/Index'
import Simu from './components/exam/Simu'
import Examing from './components/exam/Examing'
import Anwser from './components/exam/Anwser'
import Examend from './components/exam/Examend'

import menu1 from './components/index/index-com/menu1'
import menu2 from './components/index/index-com/menu2'

import indexIn from './components/index/index-com/index-in'
import content from './components/index/index-com/content'


Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'Index',
      component: Index,
      redirect: '/index/content',
      children: [
        {
          path: '/index/content',
          name: 'content',
          component: content,
          redirect: '/index/content/index-in',
          children: [
            {
              path: '/index/content/index-in',
              name: 'indexIn',
              component: indexIn
            }
          ]
        },
        {
          path: '/index/simu',
          name: 'Simu',
          component: Simu
        },
        {
          path: '/index/menu1',
          name: 'menu1',
          component: menu1
        },
        {
          path: '/index/menu2',
          name: 'menu2',
          component: menu2
        }
      ]
    },
    {
      path: '/examing',
      name: 'Examing',
      component: Examing
    },
    {
      path: '/anwser',
      name: 'Anwser',
      component: Anwser
    },
    {
      path: '/examend',
      name: 'Examend',
      component: Examend
    }
  ]
})
